.paperContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    color: #000;
    margin-top: 35px;

}

.form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 33px;
}

.text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #000;
    opacity: 0.5;
}
