.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  .title {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 1.2rem;
  }

  .form {
    display: flex;
    flex-direction: column;
    width: 413px;
    height: 528px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    align-items: center;
    justify-content: space-around;
    position: relative;

    .image {
      width: 100px;
      height: 100px;
      position: relative;

      .avatar {
        height: 100%;
        width: 100%;
        border-radius: 40px;
      }

      .camera {
        position: absolute;
        width: 30px;
        height: 30px;
        bottom: 0;
        right: 0;
        border-radius: 40%;
        background-color: grey;

        img {
          height: 90%;
        }
      }

    }

    .name {
      height: 20px;
      padding: 10px;
    }

    .email {
      padding: 10px;
      height: 20px;
    }

  }
}
